<template>
    <div @load="index" ref="svg" class="主页2"></div>
</template>

<script>
    import { animate } from "@/function/animate.js";
    export default {
        async mounted() {
            this.$refs.svg.innerHTML = await fetch("./主页面.svg").then((res) => res.text());
            this.index();
            this.event();
        },
        data() {
            return {
                svg: "",
            };
        },
        methods: {
            index() {
                let svg = this.$refs.svg;
                let $ = (...arr) => {
                    return svg.querySelectorAll(arr);
                };
                const anime = this.$anime;
                this.cloud(anime, $);
                this.boom(anime, $);
            },
            to(i) {
                console.log("/" + i + "/index");
                this.$router.push(encodeURI("/" + i + "/index"));
            },
            event() {
                let svg = this.$refs.svg;
                let $ = (...arr) => {
                    return svg.querySelector(arr);
                };
                ["体育项目馆", "中国馆", "奥运风采馆"].forEach((i) => {
                    let item = $("#" + i);
                    item.classList.add("selector");
                    item.addEventListener("click", () => {
                        animate(item, "bounce");
                        this.to(i);
                    });
                    item.addEventListener("mouseenter", () => {
                        animate(item, "bounce");
                    });
                });
            },
            cloud(anime, $) {
                $("#云1", "#云2").forEach((i) => (i.style.opacity = 0));
                anime({
                    targets: $("#云1", "#云2"),
                    keyframes: [
                        {
                            translateX: [-300, 300],
                            duration: 6000,
                            opacity: [0, 1, 0],
                        },
                    ],

                    loop: true,
                    direction: "reverse",
                    easing: "linear",
                    dalay: anime.stagger(2000),
                });
            },
            boom(anime, $) {
                anime({
                    targets: $("#气球1", "#气球2", "#气球3"),

                    translateY: -200,
                    duration: 3000,
                    translateX: [5, -5, 5, -5],
                    opacity: [0, 1, 0],
                    loop: true,
                    easing: "linear",
                    dalay: anime.stagger(200),
                });
                anime({
                    targets: $("#气球阴影", "#气球阴影-2"),
                    keyframes: [{ rx: 0, ry: 0, opacity: 0 }],
                    duration: 3000,
                    loop: true,
                    easing: "linear",
                    dalay: anime.stagger(2000),
                });
                anime({
                    targets: $("#横幅"),

                    duration: 3000,
                    translateY: [5, -5, 5, -5],

                    loop: true,
                    direction: "alternate",
                    easing: "easeInOutQuad",
                });

                anime({
                    targets: $("#波纹 > line"),

                    duration: anime.stagger(500),
                    opacity: [...Array(5).keys()].map((i) => i % 2),
                    loop: true,
                    direction: "alternate",
                    easing: "linear",
                });
                anime({
                    targets: $("#波纹 > path"),
                    duration: 5000,
                    opacity: [0, 1, 0, 1, 0],
                    translateX: 20,
                    loop: true,
                    easing: "linear",
                    delay: anime.stagger(500),
                });
            },
        },
    };
</script>
<style lang="less">
    .主页2 {
        text-align: center;

        background-color: #f7f7f7;
        .selector {
            cursor: pointer;
        }
        svg {
            overflow: visible;
            height: 100%;
            [id="文字"] {
                animation: float 3s infinite alternate;
            }
            [id="文字-2"] {
                animation: float 3s infinite 1s alternate;
            }
            [id="文字-3"] {
                animation: float 3s infinite 2s alternate;
            }
        }
    }
    @keyframes float {
        0% {
            transform: translateY(0);
        }
        100% {
            transform: translateY(-20px);
        }
    }
</style>
